import React from "react";
import Card from "./components/Card";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import { Link, Route, Switch } from "react-router-dom";
import Switches from "./Switches";
import NetNodes from "./NetNodes";
import Vlans from "./Vlans";
import "./App.css";
import Streams from "./Streams";
import Summary from "./components/Summary";

function Dashboard() {
  var localComponents = [];
  localComponents.push(
    <Paper className="myPaper">
      <div>
        <Grid container spacing={24}>
          <Grid item xs>
            <Card
              pageName=""
              content={
                <div>
                  {/* <p>  </p> */}
                  <Summary model="netNodes"/>
                  <Link to="/">...</Link>
                </div>
              }
            />
          </Grid>
          <Grid item xs>
            <Card
              pageName=""
              content={
                <div>
                  <Summary model="switches"/>
                  <Link to="/">...</Link>
                </div>
              }
            />
          </Grid>
          </Grid>

          <Grid container spacing={24}>

         
          <Grid item xs>
            <Card
              pageName="VLAN"
              content={
                <div>
                  {/* <p>  VLAN</p> */}
                  <Summary model="vlans"/>

                  <Link to="/VLAN">...</Link>
                </div>
              }
            />
          </Grid>
          <Grid item xs>
            <Card
              pageName=" "
              content={
                <div>
                  <Summary model="streams"/>
                  <Link to="/ ">...</Link>
                </div>
              }
            />
          </Grid>
          </Grid>
         
      </div>
    </Paper>
  );
  return (
    <div>
    <Switch>
      <Route path="/" component={Switches} />
      <Route path="/" component={NetNodes} />
      <Route path="/VLAN" component={Vlans} />
      <Route path="/ " component={Streams} />

    </Switch>
  </div>,
  localComponents);
}

export default Dashboard;
